
#app {
  margin: 10px auto;
  width: 800px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 18px;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
}
.cp-form_item {
  margin-bottom: 5px;
}
.cp-form_item label {
  display: inline-block;
  width: 150px;
}
.cp-form_item input,.cp-form_item select,.cp-form_item textarea {
  display: inline-block;
  width: 400px;
  line-height: 24px;
  box-sizing: border-box;
  font-size: 14px;
}
.cp-form_tip {
  font-size: 14px;
}



